const chuyenDoiUrl = (str) => {
  let english = /[\u3400-\u9FBF]/.test(str); // -> true => chinese, japanese language
  if (!english) {
    // Chuyn ht sang ch thng
    str = str.toLowerCase();
    // xa du
    str = str.replace(/(||||||||||||||||)/g, "a");
    str = str.replace(/(||||||||||)/g, "e");
    str = str.replace(/(||||)/g, "i");
    str = str.replace(/(||||||||||||||||)/g, "o");
    str = str.replace(/(||||||||||)/g, "u");
    str = str.replace(/(||||)/g, "y");
    str = str.replace(/()/g, "d");
    // Xa k t c bit
    str = str.replace(/([^0-9a-z-\s])/g, "");
    // Xa khong trng thay bng k t -
    str = str.replace(/(\s+)/g, "-");
    // xa phn d -  u
    str = str.replace(/^-+/g, "");
    // xa phn d -  cui
    str = str.replace(/-+$/g, "");
    // return
    return str;
  } else {
    return "undefined";
  }
};
export default chuyenDoiUrl;
